


























module.exports =
  name: 'logoEntry'
  components:
    IntroText: require './IntroText'

  props:
    entryTimer: default: null

  data: ->
    logoDone: false

  methods:
    emitLogoDone: ->
      @logoDone = true
      @$emit('logoDone')

  computed:
    port: ->  return @$store.state.port





